"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { 
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow 
} from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { 
  Pagination, 
  PaginationContent,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/ui/pagination";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import Image from "next/image";

interface Product {
  id?: number;
  spuId: string;
  storeId: string;
  title?: string;
  subTitle?: string;
  image?: string;
  price?: string;
  stockQuantity?: number;
  isAvailable?: boolean;
  isImport?: boolean;
}

export default function ProductsPage() {
  const router = useRouter();
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const itemsPerPage = 10;

  // Mock data - replace with actual API call
  useEffect(() => {
    // Sample mock data for now
    const mockProducts = Array.from({ length: 23 }, (_, i) => ({
      id: i + 1,
      spuId: `SPU${i + 1000}`,
      storeId: `STORE${Math.floor(Math.random() * 5) + 1}`,
      title: `Product ${i + 1}`,
      subTitle: `This is product ${i + 1}`,
      image: "https://placehold.co/100x100",
      price: `${Math.floor(Math.random() * 1000) + 100}.00`,
      stockQuantity: Math.floor(Math.random() * 100),
      isAvailable: Math.random() > 0.2,
      isImport: Math.random() > 0.7,
    }));
    
    setProducts(mockProducts);
    setTotalPages(Math.ceil(mockProducts.length / itemsPerPage));
    setLoading(false);
  }, []);

  // Filter products based on search
  const filteredProducts = products.filter(product =>
    product.title?.toLowerCase().includes(search.toLowerCase()) ||
    product.spuId.toLowerCase().includes(search.toLowerCase()) ||
    product.storeId.toLowerCase().includes(search.toLowerCase())
  );

  // Paginate the results
  const paginatedProducts = filteredProducts.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  // Navigate to product import page
  const handleImportProducts = () => {
    router.push('/products/import');
  };

  return (
    <div className="container mx-auto py-8">
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle>Products Management</CardTitle>
            <Button onClick={handleImportProducts}>Import Products</Button>
          </div>
        </CardHeader>
        <CardContent>
          <div className="flex justify-between mb-6">
            <div className="w-1/3">
              <Input
                placeholder="Search products..."
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                className="w-full"
              />
            </div>
          </div>

          {loading ? (
            <div className="text-center py-8">Loading products...</div>
          ) : (
            <>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Image</TableHead>
                    <TableHead>SPU ID</TableHead>
                    <TableHead>Store ID</TableHead>
                    <TableHead>Title</TableHead>
                    <TableHead>Price</TableHead>
                    <TableHead>Stock</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Import</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {paginatedProducts.map((product) => (
                    <TableRow key={product.id || `${product.spuId}-${product.storeId}`}>
                      <TableCell>
                        {product.image ? (
                          <div className="relative w-12 h-12">
                            <img 
                              src={product.image} 
                              alt={product.title || "Product image"} 
                              className="object-cover w-full h-full rounded"
                              width={48}
                              height={48}
                            />
                          </div>
                        ) : (
                          <div className="w-12 h-12 bg-gray-200 rounded"></div>
                        )}
                      </TableCell>
                      <TableCell>{product.spuId}</TableCell>
                      <TableCell>{product.storeId}</TableCell>
                      <TableCell>
                        <div>
                          <div className="font-medium">{product.title}</div>
                          <div className="text-sm text-gray-500">{product.subTitle}</div>
                        </div>
                      </TableCell>
                      <TableCell>¥{product.price}</TableCell>
                      <TableCell>{product.stockQuantity}</TableCell>
                      <TableCell>
                        <span className={`px-2 py-1 rounded text-xs ${product.isAvailable ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}`}>
                          {product.isAvailable ? 'Available' : 'Unavailable'}
                        </span>
                      </TableCell>
                      <TableCell>
                        <span className={`px-2 py-1 rounded text-xs ${product.isImport ? 'bg-blue-100 text-blue-800' : 'bg-gray-100 text-gray-800'}`}>
                          {product.isImport ? 'Import' : 'Local'}
                        </span>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>

              {filteredProducts.length === 0 && (
                <div className="text-center py-8">No products found matching your search criteria.</div>
              )}

              <Pagination className="mt-6">
                <PaginationContent>
                  <PaginationItem>
                    <PaginationPrevious 
                      onClick={() => setCurrentPage(p => Math.max(p - 1, 1))}
                      disabled={currentPage === 1} 
                    />
                  </PaginationItem>
                  
                  {Array.from({ length: Math.min(5, totalPages) }).map((_, i) => {
                    // Show pages around current page
                    let pageNum = i + 1;
                    if (totalPages > 5 && currentPage > 3) {
                      pageNum = currentPage - 2 + i;
                      if (pageNum > totalPages) pageNum = totalPages - (4 - i);
                    }
                    
                    return (
                      <PaginationItem key={pageNum}>
                        <PaginationLink
                          isActive={currentPage === pageNum}
                          onClick={() => setCurrentPage(pageNum)}
                        >
                          {pageNum}
                        </PaginationLink>
                      </PaginationItem>
                    );
                  })}
                  
                  <PaginationItem>
                    <PaginationNext 
                      onClick={() => setCurrentPage(p => Math.min(p + 1, totalPages))}
                      disabled={currentPage === totalPages} 
                    />
                  </PaginationItem>
                </PaginationContent>
              </Pagination>
            </>
          )}
        </CardContent>
      </Card>
    </div>
  );
} 